@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.map{border:3px solid #2f4353;height:100vh;position:relative;box-sizing:border-box}.map .robotGeneratorWrapper{height:25%;width:30%;position:absolute;left:45%;top:50%;z-index:1}.map .robotGeneratorWrapper div{margin-top:10px}.map .counter{width:30%;color:white}.map .robot{-webkit-animation:robotMover 2s infinite  alternate;animation:robotMover 2s infinite  alternate}@-webkit-keyframes robotMover{0%{-webkit-transform:translateX(30px);transform:translateX(30px)}100%{-webkit-transform:translateX(-30px);transform:translateX(-30px)}}@keyframes robotMover{0%{-webkit-transform:translateX(30px);transform:translateX(30px)}100%{-webkit-transform:translateX(-30px);transform:translateX(-30px)}}.map .rocket{border:none;width:100%;height:100%;background:url(data:image/png;base64,);background-repeat:no-repeat}.map .rocket:hover{cursor:pointer}.map .rocket:active{width:100%;height:100%;background:url(data:image/png;base64,);background-repeat:no-repeat;-webkit-animation:rocketMover 0.5s infinite  alternate;animation:rocketMover 0.5s infinite  alternate}@-webkit-keyframes rocketMover{0%{-webkit-transform:translateY(0);transform:translateY(0)}100%{-webkit-transform:translateY(-20px);transform:translateY(-20px)}}@keyframes rocketMover{0%{-webkit-transform:translateY(0);transform:translateY(0)}100%{-webkit-transform:translateY(-20px);transform:translateY(-20px)}}.map .planetBrown{height:200%;width:200%;background:linear-gradient(to left, #af6d02, #93291e);position:fixed;border-radius:60%;z-index:0;top:200px;right:50px}.map .planetPartlyGreen{height:200%;width:200%;background:linear-gradient(to left, #a38b05, #286914);position:fixed;border-radius:60%;z-index:0;top:200px;right:50px}.map .planetTotalGreen{height:200%;width:200%;background:linear-gradient(to left, #08e62d, #093b02, #08e62d);position:fixed;border-radius:60%;z-index:0;top:200px;right:50px}.map .planetGlobalWarming{height:200%;width:200%;background:linear-gradient(to left, #d45906, #93291e);position:fixed;border-radius:60%;z-index:0;top:200px;right:50px}.map .mountains{width:40%;height:auto;z-index:1;position:fixed;top:15%;left:5%}.map .cloud{width:10%;height:auto;z-index:1;position:fixed;top:4%;left:30%}.map .river{width:20%;height:50%;z-index:2;position:fixed;top:39%;left:1%}.map .skyscrapers{width:auto;height:10%;z-index:4;position:fixed;top:60%;left:20%}.map .tippi{width:auto;height:10%;z-index:4;position:fixed;top:60%;left:60%}.map .forest{position:fixed;top:50%;left:50%;z-index:3}.map .dinos{position:fixed;top:50%;left:45%;z-index:3}.map .windmill{position:fixed;top:80%;left:55%;z-index:3}.map .fabric{position:fixed;height:18%;top:80%;left:20%;z-index:3}.map .pickaxe{position:fixed;height:10%;top:20%;left:18%;z-index:3}.map .farm{position:fixed;height:15%;top:80%;left:2%;z-index:3}

.Store{border:10px solid #2f4353;background-image:linear-gradient(315deg, #2f4353 0%, #d2ccc4 74%);height:100vh;background-color:lightblue;box-sizing:border-box;z-index:2}.Store .btn{border:3px solid #2f4353;background-image:linear-gradient(360deg, #2f4353 0%, #d2ccc4 74%);height:100px;width:100%;display:flex;flex-direction:row;justify-content:space-between;padding-top:10px}.Store .btn .addOnicon{height:70px;width:auto}.Store .btn .stats{display:flex;flex-direction:column;text-align:left;padding:5px}

body{margin:0;padding:0;box-sizing:border-box;font-family:'Roboto', sans-serif;background-image:url(/kabijrocket_game/static/media/dark_sky_by_dennis_rochel.29b0fe59.jpg);background-size:cover}.gameWrapper{display:grid;grid-template-columns:17.8fr 5fr;grid-template-rows:1fr;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:"Map Store";width:100%;height:100%}.gameWrapper .Map{grid-area:Map}.gameWrapper .Store{grid-area:Store}

